<template>
    <div class="modalBg" v-show="openModal"
         transition="dialog-bottom-transition"
         scrollable
         style="
        position: fixed;
        z-index: 555;
        top: 5%;
        left: 50%;
        width: 49%;
        height: 100%;"
    >
        <v-card tile>
            <v-toolbar
                    flat
                    dark
                    color="#F5DF4D">
                <v-btn
                        icon
                        dark
                        @click="$emit('close', false)"
                >
                    <v-icon>mdi-close</v-icon>
                </v-btn>

                <v-toolbar-title >QR결제내역</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn
                            dark
                            text
                            @click="$emit('close', false)"
                    >
                        저장
                    </v-btn>
                </v-toolbar-items>
            </v-toolbar>
            <v-card-text style="background-color: #939597">
                <v-col cols="4">
                    <!--<v-card style="background-color: #F5DF4D">-->
                    <v-btn
                            color="#F5DF4D"
                            large
                            class="ma-2"
                            @click="dialog2 = !dialog2"
                    >
                        결제금액 설정
                    </v-btn>
                    <!--</v-card>-->
                </v-col>
                <v-spacer></v-spacer>
                <v-divider class="mb-4 mt-2"></v-divider>
                <v-data-table
                        height="65vh"
                        :headers="headers"
                        :items="desserts"
                        hide-default-footer
                        :page.sync="page"
                        :items-per-page="itemsPerPage"
                        class="elevation-1"
                        @page-count="pageCount = $event"
                ></v-data-table>
                <v-pagination
                        color="black"
                        class="pt-6"
                        v-model="page"
                        :length="pageCount"
                ></v-pagination>
                <v-divider></v-divider>
            </v-card-text>
        </v-card>
        <v-dialog
                v-model="dialog2"
                max-width="500px"
                hide-overlay
                style="position: absolute;">
            <v-card>
                <v-card-title>
                    결제금액설정
                </v-card-title>
                <v-card-text>
                    <v-row>
                        <v-col cols="4">
                            성인
                        </v-col>
                        <v-col cols="8">
                            <v-text-field
                                    suffix="원"
                                    v-model="priceInfo.adult"></v-text-field>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="2">
                            청소년
                        </v-col>
                        <v-col cols="10">
                            <v-text-field
                                    suffix="원"
                                    v-model="priceInfo.teenager"></v-text-field>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="2">
                            어린이
                        </v-col>
                        <v-col cols="10">
                            <v-text-field
                                    :counter="10"
                                    suffix="원"
                                    required
                                    v-model="priceInfo.child"
                            ></v-text-field>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-card-actions>
                    <v-btn
                            color="primary"
                            text
                            @click="setclose()"
                    >
                        취소
                    </v-btn>
                    <v-btn
                            color="primary"
                            text
                            @click="setPrice()"
                    >
                        저장
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import {QRpayCheckHistory} from '../api/index.js';
    import { eventBus } from "../main";
    export default {
        components:{
        },
        props: {
            showModal:{ type:Boolean, default: false },
        },
        data(){
            return{
                priceInfo: {
                    adult: 0,
                    teenager: 0,
                    child: 0
                },
                openModal: this.showModal,
                dialog2:false,
                page: 1,
                pageCount: 0,
                itemsPerPage: 15,
                headers:[
                    { text: 'NO', width:"5%",align: 'start', sortable: false, value: 'idx',},
                    { text: '결제일자',width:"40%", align: 'start', sortable: false, value: 'paymentDate',},
                    { text: '타입',width:"30%", align: 'start', sortable: false, value: 'type',},
                    { text: '결제금액',width:"15%", align: 'start', sortable: false, value: 'amount',},
                    { text: '결제여부',width:"20%", align: 'start', sortable: false, value: 'status',},
                ],
                desserts: [],
                total:''
            }
        },
        created() {


            this.priceInfo = JSON.parse(localStorage.getItem('priceInfo'));
            eventBus.changePrice(localStorage.getItem('priceInfo'));

            eventBus.$on("payList", payList => {
                console.log('이벤트버스',payList)
                this.desserts =[];
                this.desserts = payList.data.content.list;
                this.total = payList.data.content.total;
                // this.$refs.desserts.refresh();
            })
            var vm = this;
            QRpayCheckHistory()
                .then(function(response){
                    vm.desserts = response.data.content.list;

                    console.log(vm.desserts)
                })
                .catch(function(error){
                    console.log(error);
                })



        },

        methods: {
            setPrice() {
                this.dialog2 = false;
                localStorage.setItem( "priceInfo",JSON.stringify(this.priceInfo));
                // console.log( localStorage.setItem)
                eventBus.changePrice(JSON.stringify(this.priceInfo));
                this.openModal = this.showModal;

            },
            setclose(){
                this.dialog2 = false;
                this.priceInfo = JSON.parse(localStorage.getItem('priceInfo'));
            }
        },
        watch: {
            showModal:function(data) {
                this.openModal = this.showModal;
                console.log(this.openModal)
            }
        },
    }
</script>
<style>
    .v-dialog__content .v-dialog__content:active {
        /*width: 50% !important;*/
        width: 50% !important;
    }
</style>
